<template>
    <div>
        <div class="box-container">
            <div class="box-btn">
                <h4>平台数据和模型使用记录</h4>
            </div>
            <!-- 数据部分 -->
            <el-table
                v-loading="loading"
                :data="recordList"
                style="width: 100%;margin-bottom: 20px;"
                >
                <el-table-column type="expand" label="计算">
                    <template slot-scope="props">
                        <div class="comp-box">
                            <div>
                                <h4>密钥</h4>
                                {{ JSON.parse(props.row.params).publicKey }}
                            </div>
                            <div class="text">
                                <h4>数据A</h4>
                                {{JSON.parse(props.row.params).A }}
                            </div>
                            <div>
                                <h4>数据B</h4>
                                {{JSON.parse(props.row.params).B }}
                            </div>
                            <div>
                                <h4>计算结果</h4>
                                {{ props.row.result }}
                            </div>
                        </div>
                        <!-- <el-form label-position="left" inline class="demo-table-expand">
                            <el-form-item label="数据A">
                                <div class="form-result">{{JSON.parse(props.row.params).A }}</div>
                            </el-form-item>
                            <el-form-item label="数据B">
                                <div>{{JSON.parse(props.row.params).B }}</div>
                            </el-form-item>
                            <el-form-item label="计算结果">
                                <div>{{ props.row.result }}</div>
                            </el-form-item>
                            <el-form-item label="公钥">
                                <div>{{ JSON.parse(props.row.params).publicKey }}</div>
                            </el-form-item>
                        </el-form> -->
                    </template>
                </el-table-column>
                <!-- <el-table-column prop="id" width="80" align="center" label="序号" sortable></el-table-column> -->
                <el-table-column prop="username" align="center" label="使用用户"></el-table-column>
                <el-table-column prop="startTime" align="center" label="使用时间"></el-table-column>
            </el-table>
        </div>
        <!-- 底部导航页 -->
        <div class="bottom-page">
            <Pagination
                :totalMsg="query.total"
                :pageSize="query.pageSize"
                :pageIndex="query.pageIndex"
                @setSize="getSize"
                @setIndex="getIndex"
            >
            </Pagination>
        </div>
    </div>
</template>

<script>
import Pagination from '@/components/Pagination.vue'
export default {
    components:{
        Pagination
    },
    data(){
        return{
            loading:false,
            recordList:[],
            username:'',
            query:{
                total:null,
                pageSize:5,
                pageIndex:1
            }
        }
    },
    methods:{
        getRecordList(){//获取用户使用数据
            let that = this;
            that.loading = true;
            this.$api.admrelist({
                page_num: that.query.pageIndex,
                page_size: that.query.pageSize,
                username: that.username
            }).then(res=>{
                let data = res.data.records.content;
                that.recordList = data;
                that.query.total = data.length;
                that.loading = false;
            })
        },
        getSize(size){//底部导航
            this.query.pageSize = size;
            this.getRecordList();
        },
        getIndex(index){
            this.query.index = index;
            this.getRecordList();
        }
    },
    created(){
        this.getRecordList();
    }
}
</script>

<style lang="scss">
    .bottom-page{
        background: #fff;
        display: flex;
        justify-content: center;
    }
    .comp-box{
        width: 100%;
        height: 100%;
        overflow-x: scroll;
    }
</style>